.hero_wrapper_about {
    background-image: url('../images/background_ap.png');
    background-size: cover;
    background-position-y: -1300px;
    text-align: left;
}

.heading {
    display: block;
    width: 50%;
    height: auto;
    margin: 2em auto 3em;
}

.project {
    padding-bottom: 15em;
}

.ap_motivation {
    padding: 6em 0;
    background-color: rgba(255, 255, 255, 0.03);
}

.ap_topic_words {
    padding: 6em 0;
}

.ap_wordcloud {
    padding: 6em 0;
    background-color: rgba(255, 255, 255, 0.03);
}

.ap_sentiment {
    padding: 6em 0;
}

.ap_top_links {
    padding: 6em 0;
    background-color: rgba(255, 255, 255, 0.03);
}

.pipeline {
    padding: 6em 0;
    padding-bottom: 15em;
}

.pipeline_content_top {
    display: flex;
}

.pipeline h4 {
}

.pipeline p {
    margin-left: 11em;
}

.pipeline_content_top p {
    margin-left: 4em;
    margin-block-end: 0;
    margin-inline-end: 0;
}

.pipeline_graph {
    margin-left: 11em;
    width: 30%;
    margin-bottom: 3em;
}

.pipeline_content {
    padding-left: 8em;
}

p {
    margin: 0.5em auto;
    font-size: x-large;
    width: 50%;
    font-family: 'Montserat';
    text-decoration: none;
}

h4 {
    font-size: xx-large;
    text-align: start;
    margin-left: 12em;
    margin-block-start: 0;
    margin-bottom: 3em;
    font-family: 'Montserat';
}