.hero_wrapper_about_us {
    background-image: url('../images/background_au.png');
    background-size: cover;
    background-position-y: -100px;
    text-align: center;
}

.heading_aboutus {
    width: 40%;
    margin-top: 10em;
    margin-bottom: 10em;
}

.one_of_us {
    padding-bottom: 10em;
}

.au_item {
    display: flex;
    text-align: left;
    margin: 0 auto;
    width: 50%;
}

.au_item p {
    margin-block-start: 0;
    margin-block-end: 0;
    margin-bottom: .6em;
}

.au_item p:first-child {
    width: 23%;
    color: gray;
}

.one_of_us h4 {
    color: rgb(184, 184, 184);
}